@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;700&family=Yusei+Magic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Nunito", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Nunito", sans-serif;
}

/* font-family: 'Nunito', sans-serif;
font-family: 'Yusei Magic', sans-serif; */

.App {
  /* text-align: center; */
  /* padding: 1rem; */
  min-height: 100vh;
}

.header {
  margin: 0;
  /* padding: 1rem; */
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-family: "Yusei Magic", sans-serif;
  background-color: #58a4b0;
}

.content {
  display: flex;
  min-height: calc(100vh - 5rem);
}

.content_left {
  flex: 1 1;
  background-color: #f7f7ff;
}

@media screen and (max-width: 600px) {
  .content {
    flex-direction: column-reverse;
  }
}

.groups_page {
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
}
.add_group {
  margin: 0.8rem;
  height: 10rem;
  width: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid rgb(201, 201, 201);
  border-radius: 15px;
}
.add_group:hover {
  cursor: pointer;
}

.group {
  margin: 0.8rem;
  height: 10rem;
  width: 10rem;
  background-color: #ffffff;
  border: 5px solid #fff;
  border-radius: 15px;
  box-shadow: 3px 3px 5px rgba(211, 211, 211, 0.5);
  display: flex;
  flex-direction: column;
}
.group:hover {
  cursor: pointer;
  box-shadow: 4px 4px 10px rgba(211, 211, 211, 0.5);
}

.heading {
  flex: 1 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  padding: 0.8rem;
  text-transform: capitalize;
  font-size: 1.4rem;
  font-weight: 700;
}
.created_on,
.last_active {
  margin: 0;
  padding: 0 0.8rem;
  font-size: 0.8rem;
  font-weight: 300;
  color: gray;
}
.last_active {
  padding-bottom: 0.8rem;
}

.modal {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 999;
}

.box {
  padding-top: 2rem;
  background-color: #ffffff;
  border-radius: 10px;
}

.modal_heading {
  margin: 0;
  padding: 0 2rem;
  font-size: 1.2rem;
  font-weight: 500;
}

input {
  margin: 1rem 2rem;
  width: 15rem;
  padding: 0.3rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 10px;
  background-color: lightgray;
}
input:focus {
  outline: none;
}
.btns {
  width: 100%;
  display: flex;
}
.save,
.cancel {
  flex: 1 1;
  padding: 1rem 0;
  text-align: center;
}
.save:hover,
.cancel:hover {
  background-color: lightgray;
  cursor: pointer;
}
.save {
  border-bottom-left-radius: 10px;
}
.cancel {
  border-bottom-right-radius: 10px;
}

.loading {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9999;
}

.tile {
  padding: 0.2rem 0.5rem;
  margin-bottom: 1rem;
}
.title,
.count {
  margin: 0;
}
.title {
  color: gray;
}
.count {
  font-size: 2rem;
  font-weight: 700;
}

.side_menu {
  padding: 2rem;
  min-width: 10vw;
}

.p_1,
.p_2 {
  margin: 0;
}
.p_2 {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 5rem;
  text-transform: capitalize;
}

@media screen and (max-width: 600px) {
  .p_2 {
    margin-bottom: 1rem;
  }
  .tiles {
    display: flex;
    justify-content: space-between;
  }
}

.task {
  min-width: 80%;
  margin: 0.5rem;
  padding: 0.8rem;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #ffffff;
  box-shadow: 3px 3px 5px rgba(211, 211, 211, 0.5);
  border-radius: 10px;
}
.task_name {
  flex: 1 1;
}
.task_name > input {
  margin: 0;
  width: 100%;
  background: transparent;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid #58a4b0;
}

.task_btns {
  display: flex;
  align-items: center;
}

.tasks_page {
  padding: 1rem;
}

.group_name {
  display: flex;
  align-items: center;
}
.group_name h1 {
  margin-left: 1rem;
  text-transform: capitalize;
}
.group_name > input {
  margin: 0;
  background: transparent;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid #58a4b0;
}

.add_task {
  min-width: 80%;
  margin: 0.5rem;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid rgb(201, 201, 201);
}

.add_task:hover {
  cursor: pointer;
}

.home {
  overflow: hidden;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(
    to right top,
    #2b4570,
    #614d8a,
    #9e4d92,
    #d74b84,
    #ff5964
  );
}

.left_div_large,
.right_div_large {
  height: 25rem;
  width: 25rem;
  background-color: #e9d086;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left_div_small,
.right_div_small {
  height: 20rem;
  width: 17rem;
  background-color: #c8963e;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  display: flex;
  align-items: center;
  padding-left: 2rem;
  color: #ffffff;
}

.right_div_small {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.sign_up_btn,
.log_in_btn {
  text-align: center;
  padding: 0.5rem;
  border-radius: 20px;
  border: 1px solid #ffffff;
}
.sign_up_btn:hover,
.log_in_btn:hover {
  cursor: pointer;
  background-color: #ffffff;
  color: #c8963e;
}

.lable {
  margin: 0.3rem;
}
.error {
  margin: 0;
  color: #ffffff;
  font-size: 0.8rem;
}
.register > input {
  margin: 0;
  background-color: #ffffff;
}
.register_btn {
  margin-top: 1rem;
  text-align: center;
  padding: 0.5rem;
  border-radius: 20px;
  color: #ffffff;
  border: 1px solid #ffffff;
}
.register_btn:hover {
  cursor: pointer;
  background-color: #ffffff;
  color: #c8963e;
}

.lable {
  margin: 0.3rem;
}
.error {
  margin: 0;
  color: #ffffff;
  font-size: 0.8rem;
}
.login > input {
  margin: 0;
  background-color: #ffffff;
}
.login_btn {
  margin-top: 1rem;
  text-align: center;
  padding: 0.5rem;
  border-radius: 20px;
  color: #ffffff;
  border: 1px solid #ffffff;
}
.login_btn:hover {
  cursor: pointer;
  background-color: #ffffff;
  color: #c8963e;
}

